import * as types from '../mutation-types'
import {$system_key} from '../../common/public-variable'
import {$getStorage,$setStorage,$removeStorage} from '../../common/js/storage'

const carts=$getStorage($system_key.CARTINFO_STORAGE)


/*carts:结构
* [
* {
* shopId
* shopName
* products:[{
* cartId
* sku
* productName
* productImage
* price
* number
* }]
* }]
*
*
*
*
*
*
* */

const state={
  cartInfo:carts?carts:[]
}

const getters={
  getCartInfo:state=>state.cartInfo
}

const actions={
  addToCart({commit},product){
    commit(types.ADD_TO_CART,{product});
  },
  deleteToCart({commit},sku){
    commit(types.DELETE_TO_CART,{sku});
  },
  checkToCart({commit},product){
    commit(types.SELECT_PRODUCT,{product});
  },
  addNumberToCart({commit},product){
    commit(types.ADD_NUMBER_TO_CART,{product});
  }
}

const mutations={
  [types.ADD_TO_CART](state,{product}){
    if(state.cartInfo&&state.cartInfo.length>0){
      let sellerItem = state.cartInfo.find(s=>s.sellerId==product.sellerId);
      if(sellerItem){
        let productItem=sellerItem.products.find(p=>p.sku==product.sku);
        if(productItem){//如果购物车里已存在该商品，直接加数量
          productItem.number+=product.number;
        }
        else
        {//如果商家存在，商品不存在，在该商家下的商品集合里添加商品信息
          sellerItem.products.unshift({
            sku:product.sku,
            productName:product.productName,
            productImage:product.productImage,
            price:product.price,
            number:product.number,
            checked:true
          });
        }
      }
      else{
        //购物车不为空，商家和商品均不存在，新添加商家及商品信息
        let pro={};
        pro.sellerId=product.sellerId;
        pro.sellerName=product.sellerName;
        pro.products=[];
        pro.products.unshift({
          sku:product.sku,
          productName:product.productName,
          productImage:product.productImage,
          price:product.price,
          number:product.number,
          checked:true
        });
        state.cartInfo.unshift(pro)
      }
    }
    else{
      //购物车为空时添加商品信息
      let sellerItem={};
      sellerItem.sellerId=product.sellerId;
      sellerItem.sellerName=product.sellerName;
      sellerItem.products=[];
      sellerItem.products.unshift({
        sku:product.sku,
        productName:product.productName,
        productImage:product.productImage,
        price:product.price,
        number:product.number,
        checked:true
      });
      state.cartInfo.unshift(sellerItem)
    }

    $setStorage($system_key.CARTINFO_STORAGE,state.cartInfo)
  },
  [types.ADD_NUMBER_TO_CART](state,{product}){
    //对购物车已有商品进行数量操作
    if(state.cartInfo&&state.cartInfo.length>0){
      state.cartInfo.forEach(function (cart,index) {
        if(cart.products&&cart.products.length>0){
          cart.products.forEach(function (pro,index) {
            if(pro.sku==product.sku) {
              if(pro.number==1&&product.number<0) {
                return;
              }
              pro.number += product.number;
              $setStorage($system_key.CARTINFO_STORAGE, state.cartInfo)
            }
          })
        }
      })
    }
  },

  [types.DELETE_TO_CART](state,{sku}){
   /* const record=state.cartInfo.find(p=>p.sku===sku);
    if(record) {
      if(record.quantity==1){
        return ;
      }
      else {
        record.quantity--;
      }
    }*/
    //setStorage(CARTINFO_STORAGE,state.cartInfo)
  },
  [types.SELECT_PRODUCT](state,{product}){
   /* const record=state.cartInfo.find(p=>p.sku===product.sku);
    if(record) {
      record.checked=!record.checked;
      setStorage(CARTINFO_STORAGE, state.cartInfo)
    }*/
  }

}
export default {
  state,
  actions,
  getters,
  mutations
}




