import Vue from 'vue'
import Router from 'vue-router'
import {isLogin} from '../common/js/checkIsLogin'
import Home from '../components/home/index'
import Product from '../components/product/product'
import Login from '../components/login/login'
import Regiter from '../components/login/regiter'
import Member from '../components/member/member'
import Cart from '../components/cart/cart'
import Set from '../components/member/set'
import store from '../store'
import * as types from '../store/mutation-types'
import Person from '../components/member/person.vue'
import Address from '../components/member/address.vue'
import EditAddress from '../components/member/editaddress.vue'
import SetPassWord from '../components/login/password'
import Retrieve from '../components/login/retrieve'
import Regarding from '../components/member/regarding'
import Reset from '../components/login/reset'
import Safety from '../components/member/safety.vue'
Vue.use(Router)
//参数说明：requiresAuth 当前路由是否需要进行登录验证
//footerShow 当前路由是否需要显示底部
//requiresOut：当前路由是否进行登录验证（比如登录页面，如果用户已经登录，直接路由打开页面，跳转到用户中心）
//title：路由标题
//inAnimation:进场动画效果
//outAnimation：离场动画效果

const routers=new Router({
  mode: 'hash',
  routes: [

    {path: '/', name: 'home', component: Home,meta:{footerShow:true}},
    {path: '/product/:sku', name: 'product', component: Product,meta:{footerShow:true}},
    {path: '/cart', name: 'cart', component: Cart,meta:{footerShow:true}},

    {path: '/login', name: 'login', component: Login,meta:{footerShow:false,requiresOut:true}},
    {path: '/regiter', name: 'regiter', component: Regiter,meta:{footerShow:false}},
    {path: '/setpassword', name: 'setpassword', component: SetPassWord,meta:{footerShow:false}},
    {path: '/member/set', name: 'set', component: Set,meta:{requiresAuth:true,footerShow:false,title:'设置'}},
    {path: '/member', name: 'member', component: Member,meta:{footerShow:true}},
    {path: '/reset', name: 'reset', component: Reset,meta:{footerShow:true}},
    {path: '/retrieve', name: 'retrieve', component: Retrieve,meta:{footerShow:false}},
    {path: '/member/safety', name: 'safety', component: Safety,meta:{footerShow:false}},
    {path: '/member/regarding', name: 'regarding', component: Regarding,meta:{footerShow:false}},
    {path: '/member/person', name: 'person', component: Person,meta:{requiresAuth:true,footerShow:false,title:'个人信息'}},
    {path: '/member/address', name: 'Address', component: Address,meta:{requiresAuth:true,footerShow:false,title:'管理收货地址'}},
    {path: '/member/editaddress', name: 'EditAddress', component: EditAddress,meta:{requiresAuth:true,footerShow:false,title:'管理收货地址'}},
  ]
})
routers.beforeEach((to,from,next)=>{//路由拦截
  if(to.meta&&to.meta.requiresAuth===true&&isLogin()==false){
    next({
      name:'login',
      query:{
        returnurl:to.fullPath
      }
    });
  }
  else if(to.meta&&to.meta.requiresOut===true&&isLogin()==true){
    next({
      name:'member'
    });
  }
  else{
    store.commit(types.UPDATELOADINGSTATUS,{value:true})//设置loading状态
    next();
  }
});
routers.afterEach(function (to) {
  store.commit(types.CURRENT_ROUTER,{router:to});
  store.commit(types.UPDATELOADINGSTATUS,{value:false})//设置loading状态
})

export default routers;








