<template>
<div style="padding-bottom: 80px">
  <div v-for="cart in cartStatistics.cartInfo">
    <div style="width: 100%;height: 30px;border: 1px solid #000">
      <input type="checkbox">
      {{cart.sellerName}}--------{{cart.sellerId}}
      <!--{{cartInfo.find(c=>c.sellerId==seller).sellerName}}-->
    </div>
    <div v-for="cartPro in cart.products">
      <div style="width: 100%;border: 1px solid #eee;height: 150px;padding-top: 25px">
        <div style="float: left;width: 10%;"><input type="checkbox" v-model="cartPro.checked" @click="check_cart(cartPro)">选择{{cartPro.checked}}</div>
        <div style="float: left;width: 40%;"><img :src="cartPro.productImage" height="100px" width="100px"></div>
        <div style="float: left;width: 50%;">{{cartPro.productName}}</div>
        <div style="float: left;width: 100%;">{{cartPro.sku}}----{{cartPro.price}}</div>
        <div style="width: 100%;height: 30px">
          <input type="button" value="-" style="float: left; width: 20px" @click="delete_cart(cartPro.sku)">
          <input v-model="cartPro.number" style="float: left;width: 50px">
          <input type="button" value="+" style="float: left;width: 20px" @click="addNumberToCart(cartPro.sku)">
        </div>
      </div>
    </div>
  </div>
  <div>数量：{{cartStatistics.chk_totalCount}}</div>
  <div>总价：￥{{cartStatistics.chk_totalMoney.toFixed(2)}}</div>
</div>
</template>

<script type="text/ecmascript-6">
import {mapGetters} from 'vuex'
  export default{
    data(){
      return {
        sellers: []
      }
    },
    computed: {
      ...mapGetters({
        cartInfo: 'getCartInfo',
        cartStatistics: 'cartStatistics'

      })
    },
    created(){

    },
    methods: {
      addNumberToCart(sku){
        this.$store.dispatch('addNumberToCart', {sku:sku,number:1})
      },
      delete_cart(sku){
        this.$store.dispatch('addNumberToCart', {sku:sku,number:-1});
      },
      check_cart(product){
        this.$store.dispatch('checkToCart',product);
      }
    }
  }



</script>


<style>

</style>



